


.treatment {
    width: 100%;
    height: 70vh;
    background: rgba(0, 0, 0, .4);
    position: relative;
}

.treatment:before {
    content: '';
    position: absolute;
    background: url('../../assets/treatment-bg2.jpg') no-repeat center center/cover;
    height: 100%;
    width: 100%;
    z-index: -1;
}

.treatment .content {
    position: absolute;
    top: 30%;
    width: 90%;
    left: 0;
    padding: 1rem;
}

.treatment h1 {
    color: var(--primary-color);
    text-align: left;
    font-size: 35px;
}

.treatment p {
    color: #f8f8f8;
    margin: 1rem 0;
    text-align: justify;
    font-family: 'Roboto', sans-serif;
}


/* ---------------------------> SECTION 1 */

.treatment-header1 h1 {
    margin: 2rem auto;
    font-size: 35px;
}

.service-1 {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin: 5rem;
}

.service-1-description {
    width: 50%;
    text-align: justify;    
}

.service-1-description h1{
    font-size: 25px; 
    text-align: left; 
    margin-bottom: -1rem;  
}

.service-1-description p {
    font-size: 15px; 
    text-align: left;   
}



.service-1-image {
    width: 40%; 
}

.service-1-image img {
    border-radius: 1rem;
    width: 100%;
}

.service-1 .revisions, .service-1 .recreation {
    font-size: 25px;
    color: var(--primary-color);
}

@media screen and (max-width:940px) {

    .treatment {
        height: 60vh;
    }

    .treatment .content {
    width: 100%;
    }

    .service-1 {
        margin: 1rem;
    }

    .service-1-image {
        display: none;
    }

    .service-1-description {
        width: 100%;    
    }


    .treatment p {
        font-size: 15px;
        color: #f8f8f8;
        margin: 1rem 0;
        text-align: justify;
    }

    .revisions, .recreation {
        margin-top: -5rem;
    }

}


